  --bg-color: #121212;
  --btn-bg-grey: #242424;
  --btn-bg: #e78d26;
  --white: #ffffff;
  --footer-ancor-font: #514f4e;
  height: 100%;
}
@font-face {
  font-family: "Babylonica";
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}
body {
  display: grid;
  color: var(--white);
  font-family: "Montserrat", sans-serif;
  background-color: var(--bg-color);
  margin: 0;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  background-size: auto 400px;
  background-repeat: no-repeat;
  background-position: -220px 89%;
  height: 100%;
}
footer {
  background-color: var(--btn-bg-grey);
  grid-row: 4/5;
  display: flex;
  justify-content: center;
}
h1 {
  font-size: 66px;
}
.container {
  margin: 0 130px 65px;
  display: grid;
  justify-items: center;
  gap: 64px;
}
.containerTitle {
  font-family: "Babylonica", cursive;
  color: var(--btn-bg);
  margin: 0 48px;
  justify-self: start;
  align-self: start;
}

.logoImg {
  height: 70px;
  width: auto;
}
nav,
.navBarBtns {
  align-items: center;
  display: flex;
  justify-content: space-between;
}
.navBarBtns {
  gap: 32px;
}
nav {
  background: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  background-repeat: no-repeat;
  background-size: auto 50%;
  background-position: right top;
  padding: 16px 66px;
}
input[type="radio"] {
  display: none;
}
input[type="radio"] + label {
  color: var(--white);
}
input[type="radio"]:checked + label,
input[type="radio"]:hover + label {
  color: var(--btn-bg);
}
.makeReservBtn,
button {
  background-color: var(--btn-bg-grey);
  padding: 8px 16px;
  border: 1px solid var(--btn-bg-grey);
  border-radius: 50dvh;
  color: var(--btn-bg);
}
button:hover,
button:active {
  background-color: var(--btn-bg);
  color: var(--white);
}
.row {
  display: flex;
  padding: 0 32px;
  gap: 8px;
}
.babylHeader {
  font-family: "Babylonica", cursive;
  color: var(--btn-bg);
}
.rowImgContainer {
  flex-basis: 40dvw;
  flex-shrink: 0;
  align-self: center;
}
.rowImg {
  width: 100%;
}

.footContent {
  grid-row: 4/5;
  background-color: var(--btn-bg-grey);
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  padding: 32px 200px;
  gap: 66px;
}

.fCardHead img {
  height: 60px;
  width: auto;
}
.fCardHead {
  font-size: 20px;
  font-weight: 700;
  color: var(--btn-bg);
}
.fCardBody ul {
  padding: 4px;
  list-style-type: none;
}
.fCardBody a {
  color: var(--footer-ancor-font);
  text-decoration: none;
}

.fCardBody a:hover {
  color: var(--white);
}
.Qr {
  width: 150px;
  height: auto;
}

.footCard {
  display: flex;
  flex-direction: column;
}
.fCardFooter img {
  width: 40px;
}
.menuItem {
  display: flex;
  background-color: var(--btn-bg-grey);
  border-radius: 16px;
  margin: 0 48px;
}
.menuItemTextArea {
  padding: 32px;
  display: flex;
  flex-direction: column;
}
.menuItemDescription {
  flex: 1;
}
.menuItemOrderBtn {
  font-size: 16px;
  background-color: var(--bg-color);
  padding: 16px 0;
}
.menuItemImgArea img {
  width: 300px;
  height: 100%;
  object-fit: cover;
  border-radius: 0 16px 16px 0;
}
.contactUsContainer {
  background-color: var(--btn-bg-grey);
  padding: 64px 128px;
  display: grid;
  grid-template-columns: 3fr 1fr;
  margin: 0 48px;
  border-radius: 16px;
  gap: 16px;
}
.contactUsForm {
  padding: 16px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.formHead {
  font-size: 24px;
}
.formHead,
.contactUsInfoHeader,
.contactUsInfoItem {
  color: var(--footer-ancor-font);
}
.line,
.line2 {
  margin-top: 8px;
  border-top: 4px solid var(--btn-bg);
  width: 5rem;
}
.contactUsInfoHeader {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 24px;
}
.inputRow {
  display: grid;
  grid-template-columns: repeat(2, minmax(300px, 1fr));
  gap: 16px;
}
.inputRow input {
  padding: 10px 4px;
}
.inputRow input,
.messageArea {
  background-color: var(--btn-bg-grey);
  border: 1px solid var(--footer-ancor-font);
  color: var(--white);
  font-size: 16px;
}
.messageArea {
  height: 40%;
  padding: 8px 4px;
}
.sendBtn {
  align-self: flex-end;
  background-color: var(--btn-bg);
  color: var(--white);
}
.sendBtn:hover,
.sendBtn:active {
  background-color: var(--bg-color);
}
.contactUsInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 40px;
  background-color: var(--bg-color);
}

.contactUsInfoImg {
  width: 100%;
}
.contactUsInfoAddress {
  font-size: 24px;
}
