class menuItem {
  #HTMLElement = document.createElement("div");
  #textArea = document.createElement("div");
  #imgArea = document.createElement("div");
  #img = document.createElement("img");
  #menuItemTitle = document.createElement("h2");
  #menuItemDescription = document.createElement("p");
  #menuItemPrice = document.createElement("p");
  #menuItemOrderBtn = document.createElement("button");
  #itemName;
  #itemDescription;
  #itemPrice;
  #itemImgDir;

  constructor(name, description, price, imgDir) {
    this.#itemName = name;
    this.#itemDescription = description;
    this.#itemPrice = price;
    this.#itemImgDir = imgDir;
  }
  getHTMLObject() {
    this.#menuItemTitle.textContent = this.#itemName;
    this.#menuItemDescription.textContent = this.#itemDescription;
    this.#menuItemPrice.textContent = `Price :${this.#itemPrice}`;
    this.#menuItemOrderBtn.textContent = "Add to Order";
    this.#img.src = this.#itemImgDir;
    this.#img.alt = this.#itemName;

    //adding classes
    this.#menuItemTitle.classList.add("menuItemTitle");
    this.#menuItemDescription.classList.add("menuItemDescription");
    this.#menuItemPrice.classList.add("menuItemPrice");
    this.#menuItemOrderBtn.classList.add("menuItemOrderBtn");
    this.#img.classList.add("menuItemImg");

    //appending items into their containers
    this.#HTMLElement.classList.add("menuItem");
    this.#textArea.classList.add("menuItemTextArea");
    this.#imgArea.classList.add("menuItemImgArea");

    this.#textArea.appendChild(this.#menuItemTitle);
    this.#textArea.appendChild(this.#menuItemDescription);
    this.#textArea.appendChild(this.#menuItemPrice);
    this.#textArea.appendChild(this.#menuItemOrderBtn);
    this.#imgArea.appendChild(this.#img);
    this.#HTMLElement.appendChild(this.#textArea);
    this.#HTMLElement.appendChild(this.#imgArea);
    return this.#HTMLElement;
  }
}
export { menuItem };
