import infoImg from "./img/infoImg.jpg";
export { contactPage };
function contactPage(container) {
  container.innerHTML = "";
  const contactUsContainer = document.createElement("div");
  contactUsContainer.classList.add("contactUsContainer");
  const contactUsForm = document.createElement("form");
  contactUsForm.classList.add("contactUsForm");
  //Headers of the form Area
  const formHead = document.createElement("div");
  formHead.textContent = "CONTACT";
  formHead.classList.add("formHead");
  contactUsForm.appendChild(formHead);
  const line = document.createElement("div");
  line.classList.add("line");
  formHead.appendChild(line);
  const formSecondHead = document.createElement("div");
  formSecondHead.textContent = "Get in Touch";
  formSecondHead.classList.add("formSecondHead");
  contactUsForm.appendChild(formSecondHead);
  // input rows of the form area
  const formRow1 = document.createElement("div");
  formRow1.classList.add("inputRow");
  const formRow2 = document.createElement("div");
  formRow2.classList.add("inputRow");
  //Create input fields
  const formName = document.createElement("input");
  formName.setAttribute("type", "text");
  formName.setAttribute("placeholder", "Name");
  const formEmail = document.createElement("input");
  formEmail.setAttribute("typee", "email");
  formEmail.setAttribute("placeholder", "Email Address");
  formRow1.appendChild(formName);
  formRow1.appendChild(formEmail);
  const formPhone = document.createElement("input");
  formPhone.setAttribute("type", "text");
  formPhone.setAttribute("inputmode", "tel");
  formPhone.setAttribute("pattern", "d+");
  formPhone.setAttribute("placeholder", "Phone Number");
  const formSubject = document.createElement("input");
  formSubject.setAttribute("type", "text");
  formSubject.setAttribute("placeholder", "Subject");
  formRow2.appendChild(formPhone);
  formRow2.appendChild(formSubject);
  const message = document.createElement("textarea");
  message.placeholder = "Message";
  message.classList.add("messageArea");
  const sendBtn = document.createElement("button");
  sendBtn.textContent = "SEND";
  sendBtn.classList.add("sendBtn");
  //appending inputs

  contactUsForm.appendChild(formRow1);
  contactUsForm.appendChild(formRow2);
  contactUsForm.appendChild(message);
  contactUsForm.appendChild(sendBtn);

  contactUsContainer.appendChild(contactUsForm);

  // contact Us restaurant location and telephone container
  const contactUsInfo = document.createElement("div");
  contactUsInfo.classList.add("contactUsInfo");
  const contactUsInfoImg = document.createElement("img");
  contactUsInfoImg.src = infoImg;
  contactUsInfoImg.classList.add("contactUsInfoImg");
  const contactUsInfoContent = document.createElement("div");
  contactUsInfoContent.classList.add("contactUsInfoContent");
  const contactUsInfoHeader = document.createElement("div");
  contactUsInfoHeader.textContent = "CONTACT";
  contactUsInfoHeader.classList.add("contactUsInfoHeader");
  const line2 = document.createElement("div");
  line2.classList.add("line2");
  contactUsInfoHeader.appendChild(line2);
  const contactUsInfoTel = document.createElement("p");
  contactUsInfoTel.classList.add("contactUsInfoItem");
  contactUsInfoTel.textContent = "Tel: (123) 958-6430";
  const contactUsInfoAddress = document.createElement("p");
  contactUsInfoAddress.textContent = "333 California, St.";
  contactUsInfoAddress.classList.add("contactUsInfoAddress");
  const contactUsInfoSubAddress = document.createElement("p");
  contactUsInfoSubAddress.textContent = "San Francisco, CA";
  contactUsInfoSubAddress.classList.add("contactUsInfoItem");

  contactUsInfo.appendChild(contactUsInfoImg);
  contactUsInfo.appendChild(contactUsInfoHeader);
  contactUsInfo.appendChild(contactUsInfoAddress);
  contactUsInfo.appendChild(contactUsInfoSubAddress);
  contactUsInfo.appendChild(contactUsInfoTel);
  contactUsContainer.appendChild(contactUsInfo);
  const contactHeader = document.createElement("h1");
  contactHeader.classList.add("containerTitle");
  contactHeader.textContent = "Contact Us";
  container.appendChild(contactHeader);
  container.appendChild(contactUsContainer);
}
