import "./style.css";
import { homePage } from "./homePage";
import { menuPage } from "./menuPage";
import Logo from "./img/logo.png";
import { contactPage } from "./contactPage";
const qlinkMenu = document.querySelector(".qlinkMenu");
const navBar = document.createElement("nav");
const myLogo = document.createElement("a");
const myLogoImg = new Image();
myLogoImg.src = Logo;
myLogoImg.classList.add("logoImg");
myLogo.appendChild(myLogoImg);
myLogo.setAttribute("href", "#");
navBar.appendChild(myLogo);
const navBarBtns = document.createElement("div");
navBarBtns.classList.add("navBarBtns");
navBar.appendChild(navBarBtns);
document.body.appendChild(navBar);
const container = document.createElement("div");
container.classList.add("container");
document.body.appendChild(container);
const pagesNameArray = ["Home", "Menu", "Contact"];
const radioBtns = new Array();
for (let i = 0; i < pagesNameArray.length; i++) {
  radioBtns[i] = document.createElement("input");
  radioBtns[i].setAttribute("type", "radio");
  radioBtns[i].setAttribute("id", `${pagesNameArray[i]}`);
  radioBtns[i].setAttribute("name", "pages");
  const radBtnLabel = document.createElement("label");
  radBtnLabel.textContent = `${pagesNameArray[i]}`;
  radBtnLabel.setAttribute("for", `${pagesNameArray[i]}`);
  const inputGrp = document.createElement("div");
  inputGrp.classList.add("inputGrp");
  inputGrp.appendChild(radioBtns[i]);
  inputGrp.appendChild(radBtnLabel);
  navBarBtns.appendChild(inputGrp);
}
const makeReservBtn = document.createElement("button");
makeReservBtn.classList.add("makeReservBtn");
makeReservBtn.textContent = "Make A Reservation";
navBar.appendChild(makeReservBtn);
qlinkMenu.addEventListener("click", () => {
  radioBtns[1].click();
});
radioBtns[0].addEventListener("change", () => {
  homePage(container);
});
radioBtns[0].click();
radioBtns[1].addEventListener("change", () => {
  menuPage(container);
});
radioBtns[2].addEventListener("change", () => {
  contactPage(container);
});
