import menuItemImgSrc from "./img/menuItemImg.jpg";
import { menuItem } from "./menuItem";
const itemsName = ["Beef Kebab", "Oven Kebab", "Beef Grill"];
function menuPage(container) {
  container.innerHTML = "";
  const title = document.createElement("h1");
  title.textContent = "Menu";
  title.classList.add("containerTitle");
  container.appendChild(title);
  for (let i = 0; i < itemsName.length; i++) {
    const newMenuItem = new menuItem(
      itemsName[i],
      "Lorem ipsum dolor sit amet, qui minim labore adipisicing minim sint cillum sint consectetur cupidatat.Lorem ipsum dolor sit amet, qui minim labore adipisicing minim sint cillum sint consectetur cupidatat.",
      `${String.fromCharCode(163)}20.00`,
      menuItemImgSrc,
    );
    container.appendChild(newMenuItem.getHTMLObject());
  }
  return container;
}
export { menuPage };
