  exploreMoreBtn.textContent = "Explore More";
  slidesRowContent.appendChild(slideHeader);
  slidesRowContent.appendChild(slideSecondary);
  slidesRowContent.appendChild(exploreMoreBtn);
  slidesRow.appendChild(slidesRowContent);
  slidesRow.appendChild(slideRowImgContainer);
  slidesRow.classList.add("row");
  container.appendChild(slidesRow);
  const aboutUsRow = document.createElement("div");
