  const aboutUsTextArea = document.createElement("div");
  aboutUsContent.appendChild(aboutUsHeader);
  const auTextAreaHeader = document.createElement("h1");
  auTextAreaHeader.classList.add("rowHeader");
  const auTextAreaContent = document.createElement("p");
  auTextAreaHeader.textContent = "Crafting Culinary Art";
